.canvas {
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.404);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.content {
  position: relative;
}

.close {
  position: absolute;
  top: 0;
  right: -35px;
}
